@import url(http://fonts.googleapis.com/earlyaccess/nanumgothic.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.slide-container {
    width: 100%;
    margin: auto; }
  
  h3 {
    text-align: center; }
  
  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 400px;
  }
  
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }
  
  .each-fade {
    display: flex;
  }
  
  .each-fade .image-container {
    width: 100%;
    overflow: hidden;
  }
  
  .each-fade .image-container img {
    width: 100%;
  }
  
  .each-fade h2 {
    width: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #adceed;
  }

  .react-slideshow-container .nav{
      height : 400px;
      border-radius: 0em;
      background-color: rgba(0,0,0,0);
      	
  }

  .react-slideshow-container .nav:first-of-type:hover{
    background: 
    linear-gradient( to right, #1b2d3f, rgba(255,255,255,0.0) )
  }

  .react-slideshow-container .nav:last-of-type:hover{
    background: 
    linear-gradient( to right, rgba(255,255,255,0.0), #1b2d3f)
  }

  .react-slideshow-container + div.indicators{
    bottom: 50px;
    position: relative;
  }

  .react-slideshow-container + div.indicators > div.active, .react-slideshow-container + div.indicators > div:hover {
    width: 14px;
    height: 14px;
    background: #fdeaa1;
  }

  .react-slideshow-container + div.indicators > div {
        width: 14px;
        height: 14px;
        background: rgba(0,0,0,0);
        border : 1px solid #fdeaa1;
        border-width: 2px;
    }
/**-----------------------------------------------------------------------------------------------                   Header */
@charset "utf-8";
#header{position:relative; z-index:9;}
.header_top{padding: 0 200px;height:60px; background:#1b2d3f; line-height:60px; color:#fff;}
.header_top p{float:left; margin-left:20px; font-size:16px; font-weight:500}
.header_top p a{color:#fff; padding:0 10px;}
.top_menu{float:right; margin-right:20px;}
.top_menu li{float:left;}
.top_menu li a{color:#fff; padding:0 20px;}
.top_menu li:hover, .top_menu li.active{
    color:#1b2d3f;font-weight:600; background-color:rgba(121, 181, 41, 0.8); text-shadow:2px 2px 3px rgba(0,0,0,0.2);
}
.m_menu{display:none;position: absolute; right: 0; background: url(data:image/png;base64,);background-position:center top;background-repeat:no-repeat; background-size: 25px; width: 50px;height: 50px; margin-top: 18px;cursor: pointer;}
.m_nav{display:none;width: 70%; position: absolute; right: 0;height:100%;background-color: lightslategray;}
/**-----------------------------------------------------------------------------------------------                    main > slide */
.slide_wrap{text-align: center;}
.v_tit{color:white;font-size:20px;font-weight: 600; padding: 10px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}
.v_txt{color:white;font-size:14px;padding: 10px 0 20px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}

/**-----------------------------------------------------------------------------------------------                    main > page */
.main_div{background-image: url(/test/static/media/rightPeople-WorldMap.dbb2e48a.png);width:100%;height: 700px;}

/**-----------------------------------------------------------------------------------------------                    footer */

footer{width:100%;height: 200px;background-color: #555;}
.footer_wrap{padding: 30px 200px; width: 100%; height: 100%;}
.f_tit{color:lightgray;font-size:20px;font-weight: 600; padding: 10px 0 15px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}
.f_txt{color:white;font-size:26px;font-weight: 600;padding: 10px 0 5px 0;text-shadow:2px 2px 3px rgba(0,0,0,0.2);}
.f_stxt{color:lightgray;font-size:12px;padding: 10px 0 5px 0;}

/**-----------------------------------------------------------------------------------------------                      버튼 공통 */
button{width: 100px; height: 40px;}
.btn01{background-color: rgba(121, 181, 41, 0.7); color : white;}
.btn01:hover{background-color: rgba(121, 181, 41, 1.0);}


/**-----------------------------------------------------------------------------------------------                      모바일 */
@media (min-width:0px) and (max-width:700px) {
    .header_top{padding:0px;}
    .footer_wrap{padding:30px;}
    .top_menu{display: none;}
    .m_menu{display: block;}
    .m_nav .open{display:block;}
}
@charset "utf-8";
/* default */
html, body, address, blockquote, div, dl, form, h1, h2, h3, h4, h5, h6, ol, p, pre, table, ul,
dd, dt, li, tbody, td, tfoot, th, thead, tr, button, del, ins, map, object,
a, abbr, acronym, b, bdo, big, br, cite, code, dfn, em, i, img, kbd, q, samp, small, span,
strong, sub, sup, tt, var, legend, fieldset {margin:0;padding:0;}

ol,ul,li,dl,dt,dd {list-style:none;}
img {max-width:100%;height:auto;vertical-align:middle;border:0 none;}
table {border-collapse:collapse;border-spacing:0;}
legend,caption {width:0px;height:0px;line-height:0;overflow:hidden;font-size:0px;visibility:hidden;}
fieldset {border:0 none;}

a {color:#333;text-decoration:none;cursor:pointer}
a:hover {text-decoration:none;}
button {border:0 none;background:none;cursor:pointer;}

/* box-sizing */
html {box-sizing:border-box;}
*, *:before, *:after {box-sizing:inherit;}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {display: block;}

/* font */
html,body {
    line-height:150%;font-size:12px;font-family:"나눔고딕","Nanum Gothic","돋움",Dotum,Arial,sans-serif;color:#333;;-webkit-text-size-adjust:none;
}
table,input,textarea,select,legend,button {
	font-family:"나눔고딕","Nanum Gothic","돋움",Dotum,Arial,sans-serif;
	font-size:12px;color:#333;vertical-align:middle;
}
pre {	
    font-family:"나눔고딕","Nanum Gothic","돋움",Dotum,Arial,sans-serif;
	font-size:14px;color:#333;
}




